<script>
import {  mapState} from 'vuex';
import Login from "@/components/Login.vue";
import Register from "@/components/Register.vue";
import Navlist from "@/components/Navlist.vue";
export default {
  name: "user",
  data() {
    return {
      condition: 1
    };
  },
  computed: {
    ...mapState(['userInfo'])
  },
  components: {
    Login,
    Register,
    Navlist
  },
  methods: {
    getCondition(data) {
      this.condition = data.num;
    },
    getToken() {
      const token = localStorage.getItem("token");
      if (token) {
        this.$axios.post("/user2/info").then(res => {
          const { code, msg, userInfo } = res.data;
          if (code == 1) {
            this.condition = 3;
          } else {
            this.condition = 1;
          }
        });
      }
    }
  },
  mounted() {
    this.getToken();
  }
};
