import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

//axios
import axios from 'axios'

//minit-ui
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
Vue.use(MintUI)

import { Header, Field, Loadmore, Indicator } from 'mint-ui';
Vue.component(Header.name, Header, Field.name, Field);
Vue.component(Loadmore.name, Loadmore);

const commonUrl = process.env.NODE_ENV == 'development' ? 'http://localhost:8877' : 'http://123.57.142.38:8877';
Vue.prototype.$commonUrl = commonUrl


//vant
import Vant from 'vant';
import 'vant/lib/index.css';

import {
    Search,
    Swipe,
    SwipeItem,
    SwipeCell,
    Button,
    Cell,
    CellGroup,
    GoodsAction,
    GoodsActionIcon,
    GoodsActionButton,
    Sku,
    Stepper,
    NavBar,
    Checkbox,
    CheckboxGroup,
    Card,
    SubmitBar,
    Dialog,
    AddressList,
    Popup,
    AddressEdit,
    Area
} from 'vant';

Vue.use(Swipe).use(SwipeItem);
Vue.use(Cell).use(CellGroup);
Vue.use(Search);
Vue.use(SwipeCell);
Vue.use(Button);
Vue
    .use(GoodsAction)
    .use(GoodsActionIcon)
    .use(GoodsActionButton);

Vue.use(Sku);
Vue.use(Stepper);
Vue.use(NavBar);
Vue.use(Checkbox).use(CheckboxGroup);
Vue.use(Card);
Vue.use(SubmitBar);
Vue.use(Dialog);
Vue.use(AddressList);
Vue.use(Popup);
Vue.use(AddressEdit);
Vue.use(Area);

// axios
axios.interceptors.request.use(function(config) {
    // loading
    Indicator.open('Loading...')
    // 
    config.url = commonUrl + config.url
    // 
    config.data = config.data || {}

    // token
    config.data.token = store.state.token

    return config
}, (error) => {
    return Promise.reject(error)
});

// axios
axios.interceptors.response.use(function(response) {
    // loading
    Indicator.close()

    // 
    return response;
}, function(error) {
    // 
    return Promise.reject(error);
});

Vue.config.productionTip = false

// axiosVue,,this.$axios
Vue.prototype.$axios = axios

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')