<template>
  <div id="app">
    <router-view />
  </div>
</template>
<script>
export default {
  data() {
    return {
      
    }
  },
  methods: {
			// 
			getUserInfo(){
				return this.$axios.post('/user2/info').then((res) => {
					const {code,msg,userInfo} = res.data;

					if(code == 1){
						// store,userInfo
						this.$store.commit('userInfo',userInfo)
					}else{
						this.$dialog.alert({
              message: msg
            })
					}
				})
			}
		},
		// mountedmounted
		created(){
			// ,,,,

			// token
			const token = localStorage.getItem('token')

			// token
			if(token){
				// token,store
				// 1.commitmutation
				this.$store.commit('token',token)
				// 2.dispatchaction
				// this.$store.dispatch('token',token)

				// 
				this.getUserInfo()
			}		
		},
}
</script>

