<template>
  <div>
    <van-nav-bar title="" left-text="" left-arrow @click-left="onClickLeft" />
    <div class="address">
      <div class="setadd" @click="goaddress">
        <i class="van-icon van-icon-add-square van-cell__left-icon"></i>
        <p></p>
      </div>
      <div class="add-desc" v-if="addsInfo!=null">
        <h5>{{`${addsInfo.name} , ${addsInfo.tel}`}}</h5>
        <p
          class="add-info"
        >{{`${addsInfo.province}${addsInfo.city}${addsInfo.county}${addsInfo.addressDetail}`}}</p>
      </div>
    </div>

    <ul>
      <li class="goodsitem" v-for="(item,index) in list" :key="index">
        <div class="goodsdesc">
          <van-card
            :price="item.goodsPrice.toFixed(2)"
            :title="item.goodsName"
            :num="item.goodsNum"
            :thumb="$commonUrl+'/static/'+item.goodsUrl"
            :thumb-link="'#/goodsDetail/'+item.goodsId"
            :centered="cent"
          />
        </div>
      </li>
    </ul>
    <van-submit-bar :price="total" button-text="" @submit="onSubmit" />
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      list: [],
      cent: true,
      total: 0,
      addsInfo: null
    };
  },
  computed: {
    ...mapState(["searchInfo"])
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    //
    getAdds() {
      let addDesc = this.$store.state.defAdd;

      this.$axios.post("/user2/getAdds").then(res => {
        this.addsInfo = res.data.list[addDesc];
      });
    },
    //
    reqCartList() {
      return this.$axios.post("/user2/orderall").then(res => {
        const { code, msg, list } = res.data;
        if (code == 1) {
          this.list = list;
        } else {
          this.$dialog.alert({
            message: msg
          });
        }
      });
    },
    //
    getTotal() {
      let sum = 0;
      for (let item of this.list) {
        sum += item.goodsPrice * item.goodsNum;
      }
      this.total = sum * 100;
    },
    //
    onSubmit() {
      this.$dialog.alert({
        message: ""
      });
    },
    //
    goaddress() {
      this.$router.push("/address");
    }
  },
  created() {
    this.reqCartList().then(() => {
      this.getTotal();
      this.getAdds();
    });
  }
};
</script>

<style lang="scss" scoped>
.address {
  text-align: left;
  padding: 0 0.5rem 0.5rem;
  background: repeat-x
    repeating-linear-gradient(
      -45deg,
      #ff6c6c 0,
      #ff6c6c 20%,
      transparent 0,
      transparent 25%,
      #1989fa 0,
      #1989fa 45%,
      transparent 0,
      transparent 50%
    );
  background-size: 100% 2%;
  background-position: 0 100%;
  margin-bottom: 0.5rem;
  .setadd {
    display: flex;
    align-items: center;
    line-height: 1.5rem;

    i {
      font-size: 0.6rem;
    }
    p {
      font-size: 0.3rem;
    }
  }
  .add-desc {
    display: flex;
    flex-direction: column;
  }
  .add-info {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.goodsitem {
  display: flex;
  justify-content: space-between;
  align-content: center;
  background-color: #fafafa;

  .van-checkbox {
    width: 10vw;
    justify-content: center;
  }
  .van-card__title {
    text-align: left;
  }
  .van-card__price {
    float: left;
  }
  .van-card {
    background-color: transparent;
  }
  .van-button--danger {
    height: 100%;
  }
  .goodsdesc {
    flex: 1;
  }
}
</style>