<template>
  <div>
    <van-nav-bar title="" left-text="" left-arrow @click-left="onClickLeft" />
    <van-address-list
      v-model="chosenAddressId"
      :list="list"
      @add="onAdd"
      @edit="onEdit"
      @select="setAddState"
    />
    <van-popup v-model="show" position="bottom" :style="{ height: '100%',backgroundColor:'#fff' }">
      <van-address-edit :area-list="areaList" @save="onSave" />
    </van-popup>
    <van-popup
      v-model="showto"
      position="bottom"
      :style="{ height: '100%',backgroundColor:'#fff' }"
    >
      <van-address-edit
        :area-list="areaList"
        :address-info="searchResult"
        show-delete
        @save="onSet"
        @delete="onDelete"
      />
    </van-popup>
  </div>
</template>


<script>
import area from "../assets/js/area";
export default {
  data() {
    return {
      chosenAddressId: 0,
      show: false,
      showto: false,
      areaList: area,
      searchResult: {
        // name: "",
        // tel: "13000000000",
        // province: "",
        // city: "",
        // county: "",
        // addressDetail: " 138  7  501 ",
        // areaCode:"110119"
      },
      list: []
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    //
    onAdd() {
      this.show = true;
    },
    //
    onEdit(item, index) {
      this.showto = true;
      const _id = item._id;
      this.$axios
        .post("/user2/getAddsOne", {
          _id
        })
        .then(res => {
          this.searchResult = res.data.desc[0];
        });
    },
    //
    onDelete(content) {
      this.showto = true;
      const _id = content._id;
      this.$axios
        .post("/user2/delAddsOne", {
          _id
        })
        .then(res => {
          this.getAddsList();
          this.showto = false;
        });
    },
    //
    onSet(content) {
      const {
        _id,
        areaCode,
        name,
        tel,
        province,
        city,
        county,
        addressDetail
      } = content;
      this.$axios
        .post("/user2/setAddsOne", {
          _id,
          areaCode,
          name,
          tel,
          province,
          city,
          county,
          addressDetail
        })
        .then(res => {
          this.getAddsList();
          this.showto = false;
        });
    },
    //
    setAddState(item, index) {
      this.$store.commit("defAdd", this.chosenAddressId);
    },
    //
    onSave(content) {
      const {
        areaCode,
        name,
        tel,
        province,
        city,
        county,
        addressDetail
      } = content;
      this.$axios
        .post("/user2/addAdds", {
          areaCode,
          name,
          tel,
          province,
          city,
          county,
          addressDetail
        })
        .then(res => {
          this.getAddsList();
          this.show = false;
        });
    },
    //
    getAddsList() {
      this.$axios.post("/user2/getAdds").then(res => {
        const addslist = [];
        res.data.list.forEach((e, index) => {
          let {
            _id,
            name,
            tel,
            areaCode,
            province,
            city,
            county,
            addressDetail
          } = e;
          addslist.push({
            id: index,
            _id: _id,
            name: name,
            tel: tel,
            areaCode: areaCode,
            address: province + city + county + addressDetail
          });
        });
        this.list = addslist;
      });
    }
  },
  mounted() {
    this.getAddsList();
  }
};
</script>

<style lang="scss" scoped>
.van-address-list__add {
  z-index: 0;
}
</style>