import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

import axios from 'axios'

/*
	,statemutation
	,action
*/

export default new Vuex.Store({
    // ,
    state: {
        token: '',
        userInfo: null,
        searchInfo: '',
        defAdd: 0
    },
    // 
    mutations: {
        token(state, data) {
            state.token = data
        },
        userInfo(state, data) {
            state.userInfo = data
        },
        searchInfo(state, data) {
            state.searchInfo = data
        },
        defAdd(state, data) {
            state.defAdd = data
        }
    },
    // ,store,statecommit
    actions: {
        token({ commit }, data) {
            // actionmutation
            commit('token', data)
        },
        myAction({ state, commit }, data) {
            // tokenid
            return axios.post('/user2/cart/add', {
                token: state.token,
                goodsId: data.goodsId,
                goodsNum: data.goodsNum,
                goodsName: data.goodsName,
                goodsPrice: data.goodsPrice,
                goodsUrl: data.goodsUrl
            })
        }
    }
})