<template>
  <div class="botnav">
    <ul>
      <li>
        <router-link to="/Home">
          <i class="iconfont icon-index"></i>
          <p></p>
        </router-link>
      </li>
      <li @click="goGoods">
        <router-link to="">
          <i class="iconfont icon-menu"></i>
          <p></p>
        </router-link>
      </li>
      <li>
        <router-link to="/Carts">
          <i class="iconfont icon-gouwu"></i>
          <p></p>
        </router-link>
      </li>
      <li>
        <router-link to="/User">
          <i class="iconfont icon-renwu"></i>
          <p></p>
        </router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "navlist",
  methods: {
    goGoods(){
      this.$store.commit("searchInfo", false);
      if(this.$route.path!="/goods"){
        this.$router.push("/goods")
      }else{
        location.reload()
      }
    }
  },
};
</script>
