<template>
  <div>
    <van-nav-bar title="" left-text="" left-arrow @click-left="onClickLeft" />
    <ul>
      <li class="goodsitem" v-for="(item,index) in list" :key="index">
        <van-checkbox :value="item.checkone" @click="revcheckone(item,index)"></van-checkbox>
        <div class="goodsdesc">
          <van-swipe-cell>
            <van-card
              :price="item.goodsPrice.toFixed(2)"
              :title="item.goodsName"
              :thumb="$commonUrl+'/static/'+item.goodsUrl"
              :thumb-link="'#/goodsDetail/'+item.goodsId"
              :centered="cent"
            >
              <div slot="num">
                <van-stepper v-model="item.goodsNum" integer @change="updateCart(item)" />
              </div>
            </van-card>
            <van-button square slot="right" type="danger" text="" @click="goodsDel(item,index)" />
          </van-swipe-cell>
        </div>
      </li>
    </ul>
    <van-submit-bar :price="total" button-text="" @submit="onSubmit" />
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      cent: true,
      total: 0
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    //
    reqCartList() {
      return this.$axios.post("/user2/cart/list").then(res => {
        const { code, msg, list } = res.data;

        if (code == 1) {
          this.list = list;
        } else {
          this.$dialog.alert({
            message: msg
          });
        }
      });
    },
    //
    updateCart(item) {
      return this.$axios
        .post("/user2/cart/update", {
          id: item._id,
          goodsNum: item.goodsNum
        })
        .then(res => {
          const { code, msg } = res.data;

          if (code == 1) {
            this.getTotal();
          } else {
            this.$dialog.alert({
              message: msg
            });
          }
        });
    },
    //
    goodsDel(item, idx) {
      this.$axios
        .post("/user2/cart/del", {
          ids: [item._id]
        })
        .then(res => {
          const { code, msg } = res.data;

          if (code == 1) {
            // 
            this.list.splice(idx, 1);
            this.getTotal();
          } else {
            this.$dialog.alert({
              message: msg
            });
          }
        });
    },
    //
    getDetail() {
      // :
      for (let item of this.list) {
        item["checkone"] = true;
      }
      this.getTotal();
    },
    //
    revcheckone(item, index) {
      item.checkone = !item.checkone;
      this.list.splice(index, 1, item);
      this.getTotal();
    },
    //
    getTotal() {
      let sum = 0;
      for (let item of this.list) {
        if (item.checkone == true) {
          sum += item.goodsPrice * item.goodsNum;
        }
      }
      this.total = sum * 100;
    },
    //
    onSubmit() {
      if (this.$store.state.token) {
        let orderlist = [];
        for (let item of this.list) {
          if (item.checkone == true) {
            orderlist.push(item)
          }
        }
        this.$axios
          .post("/user2/order", {
            orderlist
          })
          .then(res => {
            if (res.data.code == 1) {
              this.$router.push("/order");
            } else {
              this.$dialog.alert({
                message: ""
              });
            }
          });
      } else {
        this.$dialog.alert({
          message: ""
        });
      }
    }
  },
  created() {
    this.reqCartList().then(() => {
      this.getDetail();
    });
  }
};
</script>

<style lang="scss" scoped>
.goodsitem {
  display: flex;
  justify-content: space-between;
  align-content: center;
  background-color: #fafafa;

  .van-checkbox {
    width: 10vw;
    justify-content: center;
  }
  .van-card__title {
    text-align: left;
  }
  .van-card__price {
    float: left;
  }
  .van-card {
    background-color: transparent;
  }
  .van-button--danger {
    height: 100%;
  }
  .goodsdesc {
    flex: 1;
  }
}
</style>