<template>
  <div class="home">
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App"/> -->
    <mt-loadmore
      :top-method="loadTop"
      :bottom-method="loadBottom"
      :bottom-all-loaded="allLoaded"
      ref="loadmore"
      :auto-fill="false"
    >
      <section>
        <div class="top">
          <div class="logo">
            <img src="../assets/img/logo.png" alt />
          </div>
          <div class="searchs">
            <van-search
              v-model="searchtext"
              placeholder=""
              show-action
              shape="round"
              @search="onSearch"
            >
              <div slot="action" @click="onSearch"></div>
            </van-search>
          </div>
        </div>
        <div class="banner">
          <van-swipe :autoplay="3000" indicator-color="white">
            <van-swipe-item>
              <img src="../assets/img/banner1.jpg" alt />
            </van-swipe-item>
            <van-swipe-item>
              <img src="../assets/img/banner2.jpg" alt />
            </van-swipe-item>
            <van-swipe-item>
              <img src="../assets/img/banner3.jpg" alt />
            </van-swipe-item>
            <van-swipe-item>
              <img src="../assets/img/banner4.jpg" alt />
            </van-swipe-item>
          </van-swipe>
        </div>
        <div class="tag">
          <p>
            <i class="iconfont icon-yuanyoujiantou"></i>
            <span>29</span>
          </p>
          <p>
            <i class="iconfont icon-yuanyoujiantou"></i>
            <span></span>
          </p>
          <p>
            <i class="iconfont icon-yuanyoujiantou"></i>
            <span></span>
          </p>
        </div>
        <div class="goodslist">
          <ul>
            <li v-for="(item,index) in list" :key="index">
              <router-link to="/Goods">
                <img :src="$commonUrl+'/static/'+item.url" alt />
                <div class="describe">
                  <p>{{item.name}}</p>
                  <p>
                    
                    <span>{{item.price}}</span>
                  </p>
                </div>
              </router-link>
            </li>
          </ul>
        </div>
      </section>
    </mt-loadmore>
    <Navlist />
  </div>
</template>

<script>
import Navlist from "@/components/Navlist.vue";

export default {
  name: "home",
  data() {
    return {
      searchtext: "",
      // 
      allLoaded: false,

      // 
      list: [],
      // 
      page: 1
    };
  },
  components: {
    Navlist
  },
  methods: {
    // ,
    loadTop() {
      // 
      this.$axios
        .post("/goods/list", {
          pageNum: 1
        })
        .then(res => {
          const { code, msg, list } = res.data;
          // 
          this.list = list;
          // 1
          this.page = 1;

          // loading
          this.$refs.loadmore.onTopLoaded();
          // 
          this.allLoaded = false;
        });
    },
    // ,
    loadBottom() {
      // +1
      const newPage = this.page + 1;

      // 
      this.$axios
        .post("/goods/list", {
          pageNum: newPage
        })
        .then(res => {
          const { code, msg, list } = res.data;

          // 
          if (list.length) {
            // 
            this.list = this.list.concat(list);

            // 
            this.page = newPage;
          } else {
            // 
            this.allLoaded = true;
          }

          // 
          this.$refs.loadmore.onBottomLoaded();
        });
    },
    onSearch() {
      this.$store.commit("searchInfo", this.searchtext);
      this.$router.push('/goods')
    }
  },
  // ,,
  mounted() {
    this.loadTop();
  }
};
</script>
