<template>
  <div>
    <mt-header title="">
      <router-link to slot="left">
        <mt-button icon="back" @click="gopre"></mt-button>
      </router-link>
    </mt-header>
    <div class="froms">
      <mt-field label="" placeholder="" v-model="username"></mt-field>
      <mt-field label="" placeholder="" type="password" v-model="password"></mt-field>
    </div>
    <mt-button type="danger" size="large" @click="onRegister"></mt-button>
  </div>
</template>

<script>
export default {
  name: "register",
  data() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    gopre() {
      this.$emit("getMessage", {
        num: 1
      });
    },
    onRegister(data) {
      const { username, password } = this;
      if (username && password) {
        // 
        this.$axios
          .post("/user/register", {
            username,
            password
          })
          .then(res => {
            const { code, msg } = res.data;

            if (code == 1) {
              // 

              this.$emit("getMessage", {
                num: 1
              });
            } else {
              this.$dialog.alert({
                message: msg
              });
            }
          })
          .catch(err => {
            this.$dialog.alert({
              message: err
            });
          });
      } else {
        this.$dialog.alert({
          message: ""
        });
      }
    }
  }
};
</script>