<template>
  <div>
    <mt-header title="">
      <router-link to="home" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <router-link to slot="right">
        <mt-button @click="toLogin"></mt-button>
      </router-link>
    </mt-header>
    <div class="froms">
      <mt-field label="" placeholder="" v-model="username"></mt-field>
      <mt-field label="" placeholder="" type="password" v-model="password"></mt-field>
    </div>
    <mt-button type="danger" size="large" @click="toUser"></mt-button>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    toLogin() {
      this.$emit("getMessage", { num: 2 });
    },
    toUser() {
      const { username, password } = this;
      this.$axios
        .post("/user/login", {
          username,
          password
        })
        .then(res => {
          const { code, msg, token } = res.data;
          if (code == 1) {
            localStorage.setItem("token", token);
            this.$store.commit("token", token);
            this.$parent.$parent.getUserInfo(token).then(() => {
              // 
              this.$emit("getMessage", {
                num: 3
              });
            });
          } else {
            this.$dialog.alert({
              message: msg
            })
          }
        });
    }
  }
};
</script>