<template>
  <div v-if="info">
    <van-nav-bar :title="info.name" left-text="" left-arrow @click-left="onClickLeft" />
    <div class="goodsimg">
      <img :src="$commonUrl+'/static/'+info.url" alt />
    </div>
    <div class="attr">
      <p class="goodsname">{{info.name}}</p>
      <span class="goodsprice">{{info.price}}</span>
    </div>
    <div class="desc">
      <h3></h3>
      <div class="info">{{info.desc}}</div>
    </div>
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="" />
      <van-goods-action-icon icon="cart-o" text="" @click="goCart"/>
      <van-goods-action-icon icon="shop-o" text="" />
      <van-goods-action-button type="warning" text="" @click="upgoodsnum" />
      <van-goods-action-button type="danger" text="" @click="toCart" />
    </van-goods-action>
    <van-sku
      v-model="showBase"
      :sku="sku"
      :goods="goods"
      :hide-stock="sku.hide_stock"
      @buy-clicked="onBuyClicked"
      @add-cart="onAddCartClicked"
      @stepper-change="goodsNum"
    />
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      id: "",
      info: null,
      goodsnum: 1,
      showBase: false,
      sku: {
        // sku
        // 
        tree: [],
        //  sku M  sku S 
        list: [],
        price: "1.00", // 
        stock_num: 227, // 
        hide_stock: true // 
      },
      goods: {
        // 
        title: "",
        //  sku 
        picture: ""
      }
    };
  },
  computed: {
    ...mapState(["token"])
  },
  methods: {
    upgoodsnum() {
      this.showBase = true;
    },
    //
    onBuyClicked() {
      if (this.$store.state.token) {
        this.$store
          .dispatch("myAction", {
            goodsId: this.id,
            goodsNum: this.goodsnum,
            goodsName: this.info.name,
            goodsPrice: this.info.price,
            goodsUrl: this.info.url
          })
          .then(() => {
            this.$router.push("/carts");
          });
      } else {
        this.$dialog
          .confirm({
            title: "",
            message: ",?"
          })
          .then(() => {
            this.$router.push("/user");
          });
      }
    },
    //
    onAddCartClicked() {
      if (this.$store.state.token) {
        // 
        this.$store
          .dispatch("myAction", {
            goodsId: this.id,
            goodsNum: this.goodsnum,
            goodsName: this.info.name,
            goodsPrice: this.info.price,
            goodsUrl: this.info.url
          })
          .then(res => {
            const { code, msg } = res.data;

            if (code == 1) {
              this.$dialog
                .confirm({
                  title: "",
                  message: "?"
                })
                .then(() => {
                  this.$router.push("/carts");
                });
            } else {
              this.$dialog.alert({
                message: msg
              });
            }
          });
      } else {
        this.$dialog
          .confirm({
            title: "",
            message: ",?"
          })
          .then(() => {
            this.$router.push("/user");
          });
      }
    },
    //
    getGoods() {
      const id = this.id;
      this.$axios
        .post("/goods/detail", {
          id
        })
        .then(res => {
          const { code, msg, info } = res.data;
          this.info = info;
          this.goods.title = info.name;
          this.goods.picture = this.$commonUrl + "/static/" + info.url;
          this.sku.price = info.price;
        });
    },
    //
    toCart() {
      if (this.$store.state.token) {
        this.$store
          .dispatch("myAction", {
            goodsId: this.id,
            goodsNum: this.goodsnum,
            goodsName: this.info.name,
            goodsPrice: this.info.price,
            goodsUrl: this.info.url
          })
          .then(() => {
            this.$router.push("/carts");
          });
      } else {
        this.$dialog
          .confirm({
            title: "",
            message: ",?"
          })
          .then(() => {
            this.$router.push("/user");
          });
      }
    },
    goCart(){
      this.$router.push("/carts");
    },
    goodsNum(data) {
      this.goodsnum = data;
    },
    onClickLeft() {
      this.$router.push("/goods");
    }
  },
  mounted() {
    this.id = this.$route.params.id;
    this.getGoods();
  }
};
</script>
<style lang="scss" scoped>
.goodsimg {
  width: 100vw;
  height: 100vw;
  img {
    width: 100%;
    height: 100%;
  }
}
.attr {
  height: 1.4rem;
  border-bottom: 0.01rem solid #666;
  padding: 0 0.5rem;
  text-align: left;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .goodsname {
    font-size: 0.4rem;
  }
  .goodsprice {
    font-size: 0.6rem;
    color: #b62022;
  }
}
.desc {
  margin-top: 0.5rem;
  & > h3 {
    font-size: 0.6rem;
  }
  .info {
    text-align: left;
    padding: 0.5rem;
  }
}
</style>
