<template>
  <div>
    <div class="goodslist">
      <mt-loadmore
        :top-method="loadTop"
        :bottom-method="loadBottom"
        :bottom-all-loaded="allLoaded"
        ref="loadmore"
        :auto-fill="false"
      >
        <div>
          <van-search
            v-model="searchtext"
            placeholder=""
            show-action
            shape="round"
            @search="onSearch"
          >
            <div slot="action" @click="onSearch"></div>
          </van-search>
        </div>
        <ul>
          <li v-for="(item,index) in list" :key="index" @click="toDetail(item._id)">
            <img :src="$commonUrl+'/static/'+item.url" alt />
            <div class="describe">
              <p>{{item.name}}</p>
              <p>
                
                <span>{{item.price}}</span>
              </p>
            </div>
          </li>
        </ul>
      </mt-loadmore>
    </div>
    <Navlist />
  </div>
</template>

<script>
import { mapState } from "vuex";
import Navlist from "@/components/Navlist.vue";

export default {
  data() {
    return {
      // 
      allLoaded: false,

      // 
      list: [],
      // 
      page: 1,
      searchtext: ""
    };
  },
  name: "goods",
  components: {
    Navlist
  },
  computed: {
    ...mapState(["searchInfo"])
  },
  methods: {
    // ,
    loadTop() {
      const searchInfo = this.$store.state.searchInfo;
      // 
      this.$axios
        .post("/goods/list", {
          pageNum: 1,
          searchInfo: searchInfo
        })
        .then(res => {
          const { code, msg, list } = res.data;
          // 
          this.list = list;
          // 1
          this.page = 1;
          // loading
          this.$refs.loadmore.onTopLoaded();
          // 
          this.allLoaded = false;
        });
    },
    // ,
    loadBottom() {
      // +1
      const newPage = this.page + 1;
      const searchInfo = this.$store.state.searchInfo;
      // 
      this.$axios
        .post("/goods/list", {
          pageNum: newPage,
          searchInfo: searchInfo
        })
        .then(res => {
          const { code, msg, list } = res.data;
          // 
          if (list.length) {
            // 
            this.list = this.list.concat(list);

            // 
            this.page = newPage;
          } else {
            // 
            this.allLoaded = true;
          }

          // 
          this.$refs.loadmore.onBottomLoaded();
        });
    },
    // 
    toDetail(id) {
      this.$router.push("/goodsDetail/" + id);
    },
    onSearch() {
      this.$store.commit("searchInfo", this.searchtext);
      this.loadTop();
    }
  },
  // ,,
  mounted() {
    this.loadTop();
  }
};
</script>

